import React from 'react';
// import { TestContext } from '../ShowPersonalPlanToUserToAdjust'
import PlanContext from '../ContextDefaults';

import styled from 'styled-components';
export const SSchedule = styled.div`
background-color: lightgreen;
padding:3%;
margin-top:35px;
`


const Schedule = ({ children }) => {
  const { plan, updatePlan } = React.useContext(PlanContext);

  const getNumberOfDays = () => {
    let numberOfDays = document.querySelector('#numberOfDays').value;
    if (numberOfDays < 1) numberOfDays = 1;
    // let daysInArray = new Array();
    let daysInArray = [];
    for (let i = 1; i <= numberOfDays; i++) {
      daysInArray.push('');
    }
    plan.schedule = daysInArray;
    updatePlan({ ...plan });
    console.log(plan);
  }

  const runSubmit = async (e,indexFrom0) => {
    console.log(e.target.value);
    plan.schedule[indexFrom0] = Number(e.target.value);
    updatePlan({ ...plan });
    console.log(plan);
  }

  return (
    <SSchedule>
      <h1>Trainings SCHEDULE</h1>


      <div>
        <h5>How many days will be one Cycle?:</h5>
        <input onChange={getNumberOfDays} id='numberOfDays' type='number'></input>
      </div>
      <br></br>
      {plan.schedule &&
        <div>
          <h5>Add trainings into the schedule (0 is default):</h5>
            {plan.schedule.map((day, index) => {
              return (
                <div key={index}>
                  <span>Day {index + 1}:</span>
                  <input onChange={(e)=>runSubmit(e,index)} value={plan.schedule[index]}></input>
                </div>)
            })}
            {/* <button type='submit' onClick={runSubmit} >Submit</button>{submitted && <span style={{paddingLeft:'10px'}} id='popUpMessage'>Schedule updated. (Submit again if any changes done.)</span>} */}
        </div>
      }


      {/* <div>
        <h5>How many days will be one Cycle?:</h5>
        <input id='numberOfDays' type='number'></input>
        <button onClick={getNumberOfDays}>Confirm</button>
      </div>
      <br></br>
      {days &&
        <div>
          <h5>Add trainings into the schedule:</h5>
          <form id='scheduleForm'>
            {days.map((day, index) => {
              return (
                <div key={index}>
                  <span>Day {index + 1}:</span>
                  <input name={`scheduleDay${index + 1}`} type='number'></input>
                </div>)
            })}
            <button type='submit' onClick={runSubmit} >Submit</button>{submitted && <span style={{paddingLeft:'10px'}} id='popUpMessage'>Schedule updated. (Submit again if any changes done.)</span>}
          </form>
        </div>
      } */}


    </SSchedule>
  );
};


export default Schedule;