const createPersonalSETStarter = (reps = 20, breakAfterInSeconds = 60) => {
  let set = {
    reps,
    duration:undefined,
    repsNotes: undefined,
    breakAfterInSeconds,
    breakNotes: undefined
  }
  return set;
}
const createPersonalTRAININGStarter = (name) => {
  let training = {
    name,
    sets: [createPersonalSETStarter()]
  }
  return training;
}

const createPersonalPLANStarter = (name) => { //just to make mongoose id
  let plan = {  //DUPLICATE CODE IN MONGOOSE SCHEMA!!!!!
    name,
    trainings: [createPersonalTRAININGStarter()],
    // schedule: [1]
    schedule: undefined, //will be array
    //not implemented yet:
    description: undefined,
    personal: 'yes-only',
    // planType: - will be assigned ionly to specific plans with date to this object's ID
  }
  return plan;
}









