import { urlRoot } from '../globalState/globalVariables';

export async function postJsonDataUrlAfterAPIpushups(urlEnd, data) {
  let url = urlRoot + '/pushups' + urlEnd;
  let dataToReturn;

  try {
    const options = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(data),
      credentials: 'include'
    };
    // options.credentials = 'include';
    const response = await fetch(url, options);
    // const response = await fetch(url, {credentials:'include'});
    // console.log("fetched response:", response);
    dataToReturn = await response.json(); // parses JSON response into native JavaScript objects
    console.log("POSTPushupJSON JSON RESPONSE:", dataToReturn);
    // console.log("type of...", typeof dataToReturn.idOfCurrentPlan);
    return dataToReturn;
  }
  catch (err) {
    console.log("!!!err in receiving json data back from POSTJson request", err);
    dataToReturn = err;
    return dataToReturn;
  }
  // return dataToReturn;
}

async function postJsonData(url, data) {
  let dataToReturn;
  try {
    const options = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(data),
      credentials: 'include'
    };
    // options.credentials = 'include';
    const response = await fetch(url, options);
    // const response = await fetch(url, {credentials:'include'});
    // console.log("fetched response:", response);
    dataToReturn = await response.json(); // parses JSON response into native JavaScript objects
    
  }
  catch (err) {
    console.log("!!!err in receiving json data back from POSTJson request", err);
    dataToReturn = err;
  }
  return dataToReturn;
}

export default postJsonData;