import { Link } from 'react-router-dom';

import styled from 'styled-components'

export const SLink = styled(Link)`
display: block;
font-size:2rem;
margin:10px;
background-color:orange;
padding: 3px;
border-radius:3px;
text-decoration:none;
text-align: center;
&:hover{ 
  background-color:#996b15;
  color:white;
  text-decoration:none;
  /* font-size:20px; */
}
/* transition: background-color, color; */
transition-duration: 0.25s;
`


const Home = (props) => {
  // let context2 = useContext(PushUpsContext);
  // context2.updateUser({name:'miro',id:23423})
  // console.log("XXXXXXXXXXXXXXXXXXXXXX", props.match);
  return (
    <div>
      <h1>Push-ups - logo,img,styled intro screen...</h1>
      <SLink to='/pushups/maxRepsTest'>Test Your Max Reps</SLink>
      <SLink to='/pushups/trainingPlans'>Training Plans</SLink>
      <SLink to='/pushups/startMyTraining'>Start Training</SLink>
      <br/><br/>
      {/* show only if user exists: */}<SLink to='/pushups/userProfile'>My profile (Stats, Settings)</SLink> 
    </div>
  );
};

export default Home;